<template>
  <div class="all">
    <!-- 栏目列表组件 -->
    <topic-list :items="items"></topic-list>
    <!-- 滑动组件 -->
    <infinite-loading @infinite="infiniteHandler" class="box"></infinite-loading>
  </div>
</template>

<script>
// 引入栏目列表组件
import topicList from '@/components/topics/TopicList';
// 引入查询数据的模块
import {getItemList} from '@/getdata/topics.js';
console.log(getItemList);
// 引入滑动组件
import InfiniteLoading from 'vue-infinite-loading';

export default {
  name: 'all',
  data () {
    return {
      // 文章列表
      items:[],
      // 页码
      page: 1,
    }
  },
  // 加载组件
  components:{
    // 栏目列表
    topicList,
    InfiniteLoading
  },
  // loading加载数据方法
  methods:{
    infiniteHandler($state) {
      setTimeout(() => {

        // 调用查询数据函数 
        getItemList({limit:5,page:++this.page}).then((data)=>{
          // 设置数据
          // this.items = data.data.data;
          // 把下一页的数据 追加到 items里
          this.items = this.items.concat(data.data.data)
        })        


        $state.loaded();
      }, 1000);
    },
  },
  // 钩子函数
  mounted: function () {
    // 调用查询数据函数 
    getItemList({limit:5,page:this.page}).then((data)=>{
      // 设置数据
      this.items = data.data.data;
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入基础样式
  @import '../../assets/sass/base.scss';
  .box{
    margin-bottom:rem(100px);
  } 
</style>



