import Vue from 'vue'
// 引入路由模块
import Router from 'vue-router'

// 引入组件
import Index from '@/components/Index'
import Me from '@/components/Me'
import Api from '@/components/Api'
import Getstart from '@/components/Getstart'

// 引入主题组件
import All from '@/components/topics/All'
import Good from '@/components/topics/Good'
import Share from '@/components/topics/Share'
import Ask from '@/components/topics/Ask'
import Job from '@/components/topics/Job'
import Dev from '@/components/topics/Dev'


// 引入文章详情组件
import Show from '@/components/topics/Show'

// 引入Loading
import Loading from '@/components/Loading'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: Index,
      children:[
        {
          path:'/',
          name:'all',
          component:All
        },
        {
          path:'good',
          name:'good',
          component:Good
        },
        {
          path:'share',
          name:'share',
          component:Share
        },
        {
          path:'ask',
          name:'ask',
          component:Ask
        },
        {
          path:'job',
          name:'job',
          component:Job
        },
        {
          path:'dev',
          name:'dev',
          component:Dev
        },
        {
          path:'show/:id',// 配置动态路由
          name:'show',
          component:Show
        }
      ]
    },
    {
      path: '/api',
      name: 'api',
      component:Api
    },
    {
      path: '/me',
      name: 'me',
      component:Me
    },
    {
      path: '/getstart',
      name: 'getstart',
      component:Getstart
    },
    {
      path:'/loading',
      name:'loading',
      component:Loading
    },
    {
      path:"/*",// *代表所有元素
      redirect:'/index',
    }
  ]
})



