<template>
  <div id="app">
    <!-- {{$route.path}} -->
     <!-- {{resPath}} -->
    <nav class="footer">
      <ul>
        <router-link to="/" tag="li" :class="resPath=='index'?'active':''">
          首页
        </router-link>
        <router-link to="/getstart" tag="li" :class="resPath=='getstart'?'active':''">
          入门
        </router-link>
        <router-link to="/api" tag="li" :class="resPath=='api'?'active':''">
          API
        </router-link>
        <router-link to="/me" tag="li" :class="resPath=='me'?'active':''">
          关于
        </router-link>
      </ul>
    </nav>
    <!-- 路由器 视图-->
    <router-view/>
  </div>
</template>

<script>
    export default {
      // 组件名
      name: 'App',
      // 计算属性
      computed:{
        resPath:function(){
          var arr = this.$route.path.split('/');
          //console.log(arr);
          return arr[1];
        }
      }
    }
</script>

<style lang="scss"> 
    // 引入基本样式
    @import  './assets/sass/base.scss';

    div#app{
        position: relative;
        // style设置
        width:100%;
        height:100%;
       nav.footer{
          position: fixed;
          bottom:0;
          left:0;
          width:100%;
          height:rem(90px);
          background:#f6f6f6;
          border-top:1px solid #b5b5b5;
          z-index:2;
          ul{
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
            -ms-flex-direction: row;
            flex-direction: row;
            -webkit-box-align: end;
            -ms-flex-align: end;
            align-items: center;
            -ms-flex-pack: distribute;
            justify-content: space-around;
            height: 100%;
            li{
              font-size:rem(28px);
              columns: #d6d6d6;
            }
            li.active{
              color:#8dbb3a;
            }
          }
      }
    }
   
</style>


