<template>
  <div class="show">
    <h2 class="conTitle">{{title}}</h2>
    <ul class="conAuthor">
      <li class="right">作者&nbsp;{{author}}</li>
      <li class="left">浏览量&nbsp;{{visit_count}}</li>
    </ul>
    <div class="content" v-html="content"></div>
  </div>
</template>
<script>
// 引入查询数据的模块
import {getArticle} from '@/getdata/topics.js';
console.log(getArticle);

export default {
  name: 'Show',
  data () {
    return {
      // 文章标题
      title:'',
      // 作者
      author:'',
      // 浏览量
      visit_count:'',
      // 文章内容
      content: ''  
    }

  },
  // 钩子函数
  mounted:function() {
    // 通过 $route 变量获取用户文章id
    var id = this.$route.params.id
    // 请求 文章的详情数据
    getArticle(id).then((data)=>{
      /*console.log(data.data.data);*/
       // 文章标题
			this.title = data.data.data.title;
      // 作者
      this.author = data.data.data.author.loginname;
      // 文章浏览量
      this.visit_count = data.data.data.visit_count;
      // 设置文章内容
      this.content = data.data.data.content;
      
    })
   
  //  console.log(this.$route.params.id);//代表数据
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  // 引入 基础 样式
  @import '../../assets/sass/base.scss';
  .show{
    width: calc(100% - 20px);
    height:auto;
    margin:0 auto;
    .conTitle{
      color:#333;
      line-height:rem(100px);
      font-size: rem(30px);
      font-family: 'Microsoft YaHei';
      text-align: center;
      font-weight:400;
    }

    .conAuthor{
      overflow: hidden;
      font-size: rem(24px);
      color:#888;
      border-bottom:rem(1px) dashed #dcdcdc;
      padding-bottom: rem(10px);
      li:before {
          content: "•";
          width: rem(20px);
          height: rem(20px);
          display: inline-block;
      }
      li.left{
        float: left;
      }
      li.right{
        float: right;
      }
    }

    .content{
      width: 100%;
      margin: rem(10px) auto;
      color:#787878;
      img{
        display:block;
        width:45%;
        margin:0 auto;
      }
      .markdown-text{
       
        p{ 
          line-height: rem(40px);
          font-size: rem(14px);
        }
        h2{
          line-height: rem(80px);
          font-weight: 400;
          font-size:rem(28px);
          color:#444;
        }
        ul li{
          display: inline-block;
          a{
            color:#004ff2;
            text-decoration: underline;
          }
          ul{
            padding:rem(10px) 0;
          }
        }
      }
    }
  }
	



</style>


