<template>
  <div class="all">
    <!-- <div class="first_logo">
      <img src="../../static/images/logo.svg" alt="首页logo">
    </div> -->
    <nav class="header_nav">
      <ul>
        <router-link to="/" tag="li" :class="(resPath == '' || resPath == undefined)?'boxActive':''">全部</router-link>
        <router-link to="/index/good" tag="li" :class="resPath == 'good'?'boxActive':''">精华</router-link>
        <router-link to="/index/share" tag="li" :class="resPath == 'share'?'boxActive':''">分享</router-link>
        <router-link to="/index/ask" tag="li" :class="resPath == 'ask'?'boxActive':''">问答</router-link>
        <router-link to="/index/job" tag="li" :class="resPath == 'job'?'boxActive':''">招聘</router-link>
        <router-link to="/index/dev" tag="li" :class="resPath == 'dev'?'boxActive':''">客户端测试</router-link>
      </ul>
    </nav>
    <!-- 加载视图区域 -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  // 计算属性
  computed:{
    resPath:function(){
      var arr = this.$route.path.split('/');
      console.log(arr);
      return arr[2];
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  // 引入基本样式表
  @import "../assets/sass/base.scss";
  
  nav.header_nav{
    position:relative;
    width:100%;
    height: auto;
    //background:#f6f6f6;
    // border-top:1px solid #b5b5b5;
    border-bottom:1px solid #b5b5b5;
    ul{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      -webkit-box-align: end;
      -ms-flex-align: end;
      align-items: center;
      -ms-flex-pack: distribute;
      justify-content: space-around;
      -ms-flex-flow: nowrap;
      flex-flow: nowrap;
      height: 100%;
      li{
        font-size:rem(28px);
        color: #999;
        padding-top:rem(28px);
        padding-bottom:rem(28px);
        position:relative;
      }
      li.boxActive{
       border-bottom:rem(3px) solid #76a21b;
       color:#76a21b;
       margin-top: rem(3px);
      }
    }
  }
  
</style>



