// 取数据
// 引入 axios 模块(封装后的axios模块)
import axios from "axios";

// 基本配置
var $url = axios.create({
    baseURL: 'https://cnodejs.org/api/v1/',
    timeout: 1000,
    headers: {'X-Custom-Header': 'foobar'}
  })
  
// 获取栏目列表
function getItemList(options){
    // console.log(options);
    // 默认的参数
    var newOptions = Object.assign({
        page:1,
        limit:5,
    },options)

    // 返回数据
    return $url({
        // 请求地址
        url:'/topics',//此处返回数据地址，
        // 请求方法
        method: 'get',
        // 参数
        params: newOptions
    })
}

// 获取文章详情
function getArticle(id) {
	return $url.get('/topic/'+id);
}

// 暴露此方法
export{getItemList,getArticle}



