<template>
  <div class="me">
    <h2>轮播图</h2>
    <div class="play">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <!-- <div class="swiper-slide" v-for="item in images">
            <img :src="item" alt="">
          </div> -->
          <div class="swiper-slide">
            <img :src="require('../../static/images/p1.jpg')" alt="">
          </div>
          <div class="swiper-slide">
            <img :src="require('../../static/images/p2.jpg')" alt="">
          </div>
          <div class="swiper-slide">
            <img :src="require('../../static/images/p3.jpg')" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "me",
  data() {
    return {
      // images:[
      //   '../../static/images/p1.jpg',
      //   '../static/images/p2.jpg',
      //   '../static/images/p3.jpg',
      //   '../static/images/p4.jpg',
      //   '../static/images/p5.jpg',
      // ]
    };
  },
  // 钩子函数
  mounted :function() {
    /* swiper */
    var mySwiper = new Swiper('.swiper-container',{
     autoplay:true,// 等同于以下设置
    })
  },
};
</script>

<!-- Add "scope
d" attribute to limit CSS to this component only -->
<style lang="scss">
  // 引入基本样式表
  @import "../assets/sass/base.scss";
  h2{text-align: center;font-size:rem(36px);font-weight: 400;line-height: rem(100px);}
  .play {
    width:calc(100% - 0.5rem);
    height: rem(400px);
    margin:rem(10px) auto;
    .swiper-slide{
      border-radius: rem(10px);
      height: rem(400px);
      overflow: hidden;
      img {
        width: 100%;
        height:auto;
      }
    }
  }
</style>



