<template>
  <div class="all">
    <!-- <loading v-if=" !items.length"></loading> -->
    <ul class="items">
      <li class="item" v-for="(item,index) in items">
          <!--用户头像-->
          <img v-lazy="item.author.avatar_url" alt="">
          <div class="tab">
            <div class="fontAtive" v-if="item.top">置顶</div>
            <div class="fontAtive" v-else-if="item.good">精华</div>
            <div class="static" v-else>{{tab[item.tab]}}</div>
          </div>
          <router-link :to="'/index/show/'+ item.id" tag="div" class="title">{{item.title}}</router-link>
          <!-- <router-link :to="'/index/show/'+item.id" tag="div" class="title">{{item.title}}</router-link> -->
      </li>
    </ul>
  </div>
</template>
<script>
// 引入Loading组件
import Loading from '@/components/Loading';

export default {
  name: 'all',
  data () {
    return {
      // 所有栏目分类
      tab:{
        good:'精华',
        share:'分享',
        ask:'问答',
        job:'招聘',
        dev:'测试'
      }
    }
  },
   // 加载组件
  components:{
    // 栏目列表
    Loading
  },
  // 接收数据
  props:['items'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入基本样式
  @import '../../assets/sass/base.scss';

  div.all{
    width: 100%;
    // height: rem(80px);
    ul.items{
      border-bottom:1px solid #ccc;
      li.item{
        display: flex;
        width: 100%;
        height:auto;
        border-bottom:rem(0.03px) solid #efefef;
        padding:rem(20px) 0;
        img{
          display:block;
          width:rem(60px);
          height:rem(60px);
          border-radius:rem(10px);
          margin-left:rem(20px);
        }  
        .tab{
          align-self: center;
          font-size:rem(22px);
          @mixin distanse($padding_tb,$padding_lf,$margin,$radius,$flex){
            padding:($padding_tb $padding_lf);
            margin-left:$margin;
            border-radius:$radius;
            flex:$flex;
          }
          .fontAtive{
            background:#76a21b;
            color:#fff;
            @include distanse(rem(6px),rem(12px),rem(20px),rem(4px),2);
          }
          .static{
            background:#dedede;
            color:#b4b4b4;
            @include distanse(rem(6px), rem(12px),rem(20px),rem(4px),2);
          }
        }
        .title{
          max-width: 78%;
          -ms-flex-item-align: center;
          align-self: center;
          margin-left: 0.13333rem;
          -ms-flex-flow: nowrap;
          flex-flow: nowrap;
          -webkit-box-flex: 8;
          -ms-flex: 8;
          flex: 8;
          font-size: 0.34667rem;
          overflow: hidden;
          -o-text-overflow: ellipsis;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
}
</style>



