<template>
    <div class="loading">
        <img :src="'../../static/images/beautityLoading.gif'" alt="">
    </div>
</template>
<script>
    export default{
        name:'loading',
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  // 引入 基础 样式
  @import '../assets/sass/base.scss';
    .loading{
        width:rem(100px);
        height:auto;
        min-height: rem(600px);
        margin:0 auto;
        margin-top:rem(400px);
        img{
            width:100%;
            margin:0 auto;
            display:block;
           
        }
    }
	
</style>


